@charset 'utf-8';
@font-face {
    // 指定图标字体的名称
    font-family: 'icomoon';
    // 指定图标字体的地址
    src:  url('fonts/icomoon.eot?3apv0g');
    src:  url('fonts/icomoon.eot?3apv0g#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?3apv0g') format('truetype'),
    url('fonts/icomoon.woff?3apv0g') format('woff'),
    url('fonts/icomoon.svg?3apv0g#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
}
